.navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 35px;
    position: relative;
    &__tabs {
        display: flex;
    }
    &__tab {
        position: relative;
        text-decoration: none;
        color: black;
        height: 35px;
        display: flex;
        align-items: center;
        padding: 0 15px;
        &:hover {
            opacity: 0.7;
        }
    }
    &__line {
        z-index: -1;
        top: 35px;
        left: 0;
        position: absolute;
        width: 100%;
        height: 35px;
        background-color: #9999;
    }
    &__wrapper {
        position: relative;
    }
    &__search {
        border-radius: 3px;
        padding: 5px;
        padding-left: 30px;
        padding-right: 30px;
        border: 2px solid #ccc;
        border-radius: 20px;
        font-size: 16px;
        color: #555;
        outline: none;
        width: 195px;
        &:focus {
            width: 500px;
            border-color: #007bff;
            box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
        }
        &-scope {
            position: absolute;
            width: 15px;
            height: 15px;
            left: 10px;
            top: 9px;
        }
    }
    &__searchclear {
        position: absolute;
        top: 0;
        font-size: 25px;
        right: 10px;
        cursor: pointer;
        color: #555;
    }
}

.activeTab {
    transition: 1s all;
}

.activeTab::after {
    content: '';
    position: absolute;
    height: 2px;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #0d6efd;
}

@media (max-width: 750px) {
    .navigation {
        &__search {
            box-sizing: border-box;
            width: 100%;
            &:focus {
                box-sizing: border-box;
                width: 100%;
            }
        }
        
    }
}